import { Base } from './base.model';
import { Cidade } from './cidade.model';
import { Entidade } from './entidade.model';

export class Feriado extends Base {
    public cidade?: Cidade;
    public entidade?: Entidade;

    public nome: string = ""; //Descrio do feriado;
    public dia: number = 1; //Dia do ms (1~31) ou dia da semana (1-7)");
    public mes: number = 1; //Ms
    public ano: number | null = null; // Ano do feriado caso seja data no recorrente");
    public recorrente: number = 1; // Se  uma data nica ou repete todos os anos");
    public abrangencia: string = "NACIONAL"; // "NACIONAL", "ESTADUAL", "MUNICIPAL" Abrangncia do feriado");
    public codigo_ibge: string | null = null; // Cdigo da UF ou do municpio (IBGE)")
    public data_inicio: Date = new Date(); /* Data de incio */
    public data_fim: Date | null = null; /* Data do fim */
    public entidade_id: string | null = null;
    public cidade_id: string | null = null;
    public uf: string | null = null; /* UF para abrangencia estadual */

    public constructor(data?: any) { super(); this.initialization(data); }
}
