export type Metadado = {    
  descricaoPlano: string,   
  concluido: Boolean,                       // define se o Plano  concludo, ou seja, se todas as suas demandas foram cumpridas.
  demandasNaoIniciadas: Demanda[],          // todas as demandas ainda no iniciadas pelo servidor
  demandasEmAndamento: Demanda[],           // totas as demandas j iniciadas (data_inicio diferente de nulo), mas ainda no concludas (data_entrega nula)
  demandasConcluidas: Demanda[],            // todas as demandas que possuem uma data de entrega no nula
  demandasAvaliadas: Demanda[],             // todas as demandas cujo campo avaliacao_id no  nulo
  demandasCumpridas: Demanda[],             // todas as demandas que possuem um tempo homologado no nulo
  horasUteisAfastamento: number,            // total das horas teis de afastamento do servidor
  horasDemandasNaoIniciadas: number,        // soma do tempo pactuado de todas as demandas ainda no iniciadas
  horasDemandasEmAndamento: number,         // soma do tempo pactuado de todas as demandas j iniciadas, mas ainda no concluidas
  horasDemandasConcluidas: number,          // soma do tempo pactuado de todas as demandas concludas
  horasDemandasAvaliadas: number,           // soma do tempo pactuado de todas as demandas avaliadas
  horasDemandasCumpridas: number,           // soma do tempo pactuado de todas as demandas cumpridas
  horasDecorridas: number,                  // total de horas que se passaram desde a data/hora de incio de vigncia do plano at a data/hora atual
  percentualDecorridoPlano: number,         // razo entre as horas teis decorridas e as horas teis totais do Plano
  horasUteisTotais: number,                 // Horas teis de trabalho no perodo de vigncia do Plano, considerando carga_horaria, feriados, fins de semana
  horasUteisDecorridas: number,             // Horas teis decorridas do Plano, considerando carga_horaria, feriados, fins de semana
  horasTotais: number,                      // total de horas compreendidas entre a data final de vigncia do plano e a data inicial de vigncia do plano
  percentualEsperado: number,               // razo entre as horas j decorridas do plano e as suas horas totais (clculo simples)
  percentualCumprido: number,               // razo entre as horas das demandas j cumpridas e o tempoTotal do plano
  percentualHorasNaoIniciadas: number,      // razo entre as horas pactuadas das demandas no-iniciadas, em relao s horas teis totais do Plano
  percentualHorasEmAndamento: number,       // razo entre as horas pactuadas das demandas iniciadas mas ainda no concluidas, em relao s horas teis totais do Plano
  percentualHorasConcluidas: number,        // razo entre as horas pactuadas das demandas concluidas, em relao s horas teis totais do Plano
  percentualHorasAvaliadas: number,         // razo entre as horas pactuadas das demandas avaliadas, em relao s horas teis totais do Plano
  percentualHorasTotaisAlocadas: number,    // razo entre as horas totais alocadas e as horas teis totais do Plano
  mediaAvaliacoes: number,                  // mdia aritmtica das notas de todas as demandas avaliadas
  produtividadeMedia: number,               // s existe quando o plano j foi concludo. Representa a mdia de produtividade de todas as demandas do plano+
  eficiencia: number                        // diviso do percentual cumprido do plano pelo seu percentual estimado
};    

export type MetadadosPlano = {    
  concluido: Boolean,                       // define se o Plano  concludo, ou seja, se todas as suas demandas foram cumpridas.
  demandasNaoIniciadas: Demanda[],          // todas as demandas ainda no iniciadas pelo servidor
  demandasEmAndamento: Demanda[],           // totas as demandas j iniciadas (data_inicio diferente de nulo), mas ainda no concludas (data_entrega nula)
  demandasConcluidas: Demanda[],            // todas as demandas que possuem uma data de entrega no nula
  demandasAvaliadas: Demanda[],             // todas as demandas cujo campo avaliacao_id no  nulo
  horasAfastamentoDecorridas: number,       // total das horas teis dos afastamentos do servidor j ocorridos
  horasDemandasNaoIniciadas: number,        // soma do tempo pactuado de todas as demandas ainda no iniciadas
  horasDemandasEmAndamento: number,         // soma do tempo pactuado de todas as demandas j iniciadas, mas ainda no concluidas
  horasDemandasConcluidas: number,          // soma do tempo pactuado de todas as demandas concludas
  horasDemandasAvaliadas: number,           // soma do tempo pactuado de todas as demandas avaliadas
  horasTotaisAlocadas: number,              // soma total do tempo pactuado de todas as demandas
  horasUteisAfastamento: number,            // total das horas teis de afastamento do servidor
  horasUteisDecorridas: number              // Horas teis de trabalho decorridas do Plano, considerando carga_horaria, feriados, fins de semana
  horasUteisTotais: number,                 // Horas teis de trabalho no perodo de vigncia do Plano, considerando carga_horaria, feriados, fins de semana
  mediaAvaliacoes: number,                  // mdia das avaliaes das demandas
  modalidade: string,                       // tipo de modalidade do plano
  percentualHorasNaoIniciadas: number,  
  usuario_id: string
};    

